@import "./variable.less";

@timeout: 200ms;
@max-width: 768px;
html {
  font-size: 16px;
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  height: 100%;
  font-family: @font-family;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  #root {
    margin: 0 auto;
    max-width: @max-width;
    min-width: 265px;
    height: 100%;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.clearfix {
  zoom: 1;
  &::after {
    content: "";
    display: block;
    clear: both;
  }
}

.fade-enter {
  opacity: 0.01;
  transform: translateY(100%);
}

.fade-enter-active {
  opacity: 1;
  transform: translateY(0%);
  transition: all @timeout ease-out;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: all @timeout ease-in;
}
.fade-appear {
  transform: translateY(100%);
}

.fade-appear-active {
  transform: translateY(0%);
  transition: all @timeout linear;
}

.no-pointer {
  pointer-events: none;
  user-select: none;
}
